/* ==========================================================================
   Portfolio Single - Small Slider layout style - begin
   ========================================================================== */

.edgtf-portfolio-single-holder {
    &.edgtf-ps-small-slider-layout {
        .edgtf-ps-image-holder {
            .edgtf-ps-image-inner {
                visibility: hidden;
            }

            .edgtf-ps-image {
                img {
                    width: 100%;
                }
            }
        }

        .edgtf-ps-content-item {
            margin: 0 0 33px;
        }
    }
}

/* ==========================================================================
   Portfolio Single - Small Slider layout style - end
   ========================================================================== */