/* ==========================================================================
   Portfolio Single - Small Gallery layout style - begin
   ========================================================================== */

.edgtf-portfolio-single-holder {
    &.edgtf-ps-small-gallery-layout {
        .edgtf-ps-image-holder {
            width: 100%;

            .edgtf-ps-image {
                float: left;

                a {
                    @include edgtfImageOverlayHoverStyle();
                }
            }
        }

        .edgtf-ps-content-item {
            margin: 0 0 33px;
        }
    }
}

/* ==========================================================================
   Portfolio Single - Small Gallery layout style - end
   ========================================================================== */