/* ==========================================================================
   Portfolio Single - Huge Images layout style - begin
   ========================================================================== */

.edgtf-portfolio-single-holder {
    &.edgtf-ps-huge-images-layout {
        padding: 0 4%;
        box-sizing: border-box;

        .edgtf-ps-image-holder {
            margin: 0 0 40px;

            .edgtf-ps-image {
                margin: 0 0 30px;

                &:last-child {
                    margin: 0;
                }
            }
        }

        .edgtf-ps-content-item {
            margin-bottom: 33px;
        }

        @include laptop-landscape {
            padding: 0 40px;
        }

        @include ipad-portrait {
            padding: 0 30px;
        }
    }
}

/* ==========================================================================
   Portfolio Single - Huge Images layout style - end
   ========================================================================== */